.list {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
}

@media (min-width: 500px) {
    .list {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1100px) {
    .list {
        grid-template-columns: repeat(3, 1fr);
    }
}
